import React from 'react'
import PTop1 from "./PTop1";
import PTop2 from "./PTop2";

function PTop() {
    return (
        <div className="p-top">
            <PTop1
                subtitle="Most watched movies by days"
                title ="Popular Movies"
            />
            <PTop2
                subtitle="Most watched movies by days"
                title ="Newest Movies"
            />
        </div>
    )
}

export default PTop
