import React from 'react'
import Item2 from "./../components/Item2";

function PTop2({subtitle, title}) {
    const list = [
        {   
            id:1001,
            image: null,
            resolution:[1080,'HD'],
            rated: 5.9,
            minutes: 90,
            cat: 'Romance Action',
            name: 'Thinking You 1',
            age: 13
        },
    ]
    return (
        <section className="p-top2">
            <div className="l-container">
                <div className="p-top2__title ">
                    <h4 className="c-subtitle">{subtitle}</h4>
                    <h3 className="c-title">{title}</h3>
                </div>
                <div className="p-top2__content row ">
                    { list.map(f => (
                        <Item2
                            id = {f.id}
                            image = {f.image}
                            resolution = {f.resolution}
                            rated = {f.rated}
                            minutes = {f.minutes}
                            name = {f.name}
                            age = {f.age}
                        />

                    ))}
                </div>
                <a href="/" className="c-btn c-btn--primary c-more">
                    BROWSE ALL MOVIES
                </a>
            </div>
        </section>

    )
}

export default PTop2
