import React from 'react'
import Item1 from "./../components/Item1";
function PTop1({subtitle, title}) {
    const list = [
        {   
            id:1,
            image: null,
            cat: 'Romance Action',
            name: 'Thinking You 1',
            chapter: 'SEASON 5 - 2020'
        },
        {   
            id:2,
            image: null,
            cat: 'Romance Action',
            name: 'Thinking You 2',
            chapter: 'SEASON 5 - 2020'
        },
        {   
            id:3,
            image: null,
            cat: 'Romance Action',
            name: 'Thinking You 3',
            chapter: 'SEASON 5 - 2020'
        },
        {   
            id:4,
            image: null,
            cat: 'Romance Action',
            name: 'Thinking You 4',
            chapter: 'SEASON 5 - 2020'
        }
    ]
    return (
        <section className="p-top1">
            <div className="l-container">
                <div className="p-top1__title ">
                    <h4 className="c-subtitle">{subtitle}</h4>
                    <h3 className="c-title">{title}</h3>
                </div>
                <div className="p-top1__content row ">
                    { list.map(film => (
                        <Item1
                            id = {film.id}
                            image = {film.image}
                            cat = {film.cat}
                            name = {film.name}
                            chapter = {film.chapter}
                        />
                    ))}
                </div>
                <a href="/" className="c-btn c-btn--primary c-more">
                    BROWSE ALL MOVIES
                </a>
            </div>
        </section>
    )
}

export default PTop1
