import React from 'react'

function MovieMainvisuals({image, videoSrc}) {
    return (
        <div className="c-mainvisuals js-mainvisuals" data-anchor="1">
            <div className="c-mainvisuals__content u-fullheight">
                <div className="p-video">
                    <img src={image ? image :`${process.env.PUBLIC_URL}/assets/img/no-img.jpg`} alt=""  className="p-video__bg" />
                    <div className="l-container">
                        <div className="p-video__content">
                            <video src={videoSrc} controls></video>
                            <a href="/" className="c-btn c-btn--primary p-video__back">
                                <i className="fas fa-chevron-left"></i>Back
                            </a>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default MovieMainvisuals
