import React from 'react'
import Item2 from "./Item2";

function Movie3() {
    const list = [
        {   
            image: null,
            resolution:[1080,'HD'],
            rated: 5.9,
            minutes: 90,
            cat: 'Romance Action',
            name: 'Thinking You 1',
            age: 13
        },
    ]
    return (
        <section className="p-movie3">
            <div className="l-container">
                <div className="p-movie3__title ">
                    <h4 className="c-subtitle">Find anywhere else</h4>
                    <h3 className="c-title">Related Movies</h3>
                </div>
                <div className="p-movie3__slide">
                    <div className="p-movie3__content row">
                        { list.map(f => (
                            <Item2
                                image = {f.image}
                                resolution = {f.resolution}
                                rated = {f.rated}
                                minutes = {f.minutes}
                                name = {f.name}
                                age = {f.age}
                            />

                        ))}
                    </div>
                    {/* <!-- <div className="p-movie3__controller js-related-controller">
                        <a href="/" className="p-movie3__prev js-related__prev">
                            <i className="fas fa-angle-left"></i>
                        </a>
                        <a href="/" className="p-movie3__next js-related__next">
                            <i className="fas fa-angle-right"></i>
                        </a>
                    </div> --> */}
                </div>
            

                <a href="/" className="c-btn c-btn--primary c-more">
                    BROWSE ALL MOVIES
                </a>
            </div>
        </section>
       
    )
}

export default Movie3
