import React from 'react'

function Movie1({name, cat, des, rated, date, director, production, screenplay}) {
    return (
        <section className="p-movie1">
            <div className="l-container">
                <div className="p-movie1__content u-flex u-between">
                    <div className="p-movie1__info">
                        <h3 className="p-movie1__title">{name}</h3>
                        <div className="p-movie1__cat">
                            <span>{cat}</span>
                        </div>
                        <p className="p-movie1__description">
                            {des}
                        </p>
                        <div className="p-movie1__actions u-flex u-a-center">
                            <a href="/" className="c-btn c-btn--primary">ADD YOUR FAVOR</a>
                            <div className="u-flex u-a-center">
                                <a href="/" className="c-icon c-like">
                                    <i className="far fa-thumbs-up"></i>
                                </a>
                                <a href="/" className="c-icon c-dislike">
                                    <i className="far fa-thumbs-down"></i>
                                </a>
                                <span>{rated} liked this film</span>
                            </div>
                        </div>
                    </div>
                    <div className="p-movie1__image">
                        <div className="gutter">
                            <img src="./assets/img/films/movies01.jpg" alt="" />
                            <div className="p-movie1__item">
                                <span className="p-movie1__item--bold">
                                    Initial release:
                                </span>
                                <span>
                                   {date}
                                </span>
                            </div>
                            <div className="p-movie1__item">
                                <span className="p-movie1__item--bold">
                                    Director:
                                </span>
                                <span>
                                    {director}
                                </span>
                            </div>
                            <div className="p-movie1__item">
                                <span className="p-movie1__item--bold">
                                    Production:
                                </span>
                                <span>
                                    {production}
                                </span>
                            </div>
                            <div className="p-movie1__item">
                                <span className="p-movie1__item--bold">
                                    Screenplay:
                                </span>
                                <span>
                                    {screenplay}
                                </span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>       
    )
}

export default Movie1
