import Index from "./pages/Index";
import SingleMovie from "./pages/SingleMovie";

import { Route, Switch } from "react-router-dom";
function App() {
  return (
    <div className="App">
        <Switch>
            <Route path="/single-movie">
                <SingleMovie/>
            </Route>
            <Route path="/">
                <Index/>
            </Route>
        </Switch>
    </div>
  );
}

export default App;
